<template>
  <section class="p-6">
    <div class="page-shell space-y-6">
      <!-- ÏÉÅÎã® ÌÉÄÏù¥ÌãÄ -->
      <header class="page-header">
        <h2 class="page-title text-2xl md:text-3xl font-bold tracking-tight">
          ÏßÄÏõêÍ≤∞Í≥º ÏàòÏ†ï
        </h2>
      </header>

      <!-- Î°úÎî© / ÏóêÎü¨ -->
      <p v-if="loading" class="text-sm text-gray-500">
        ÏßÄÏõêÍ≤∞Í≥º Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ëÏûÖÎãàÎã§...
      </p>
      <p v-if="error" class="text-sm text-red-500">{{ error }}</p>

      <!-- ‚úÖ Í∏∞Î≥∏Ï†ïÎ≥¥ Ïπ¥Îìú (ÏûëÏÑ± ÌôîÎ©¥Í≥º ÎèôÏùº ÌòïÌÉú) -->
      <div class="meta-card">
        <div class="meta-grid">
          <!-- 1. ÏßÄÏõêÏûê -->
          <div class="meta-item">
            <span class="meta-label">ÏßÄÏõêÏûê</span>
            <span class="meta-value">
              {{ submitInfo.childName || "Î≥∏Ïù∏" }}
            </span>
          </div>

          <!-- 2. Î≥¥Ìò∏Ïûê -->
          <div class="meta-item">
            <span class="meta-label">Î≥¥Ìò∏Ïûê</span>
            <span class="meta-value">
              {{ submitInfo.guardianName || "-" }}
            </span>
          </div>

          <!-- 3. Îã¥ÎãπÏûê -->
          <div class="meta-item">
            <span class="meta-label">Îã¥ÎãπÏûê</span>
            <span class="meta-value">
              {{ submitInfo.assigneeName || "-" }}
            </span>
          </div>

          <!-- 4. Ïû•Ïï†Ïú†Ìòï -->
          <div class="meta-item">
            <span class="meta-label">Ïû•Ïï†Ïú†Ìòï</span>
            <span class="meta-value">
              {{ submitInfo.disabilityType || "-" }}
            </span>
          </div>

          <!-- Ïö∞ÏÑ†ÏàúÏúÑ -->
          <div class="meta-item">
            <span class="meta-label">Ïö∞ÏÑ†ÏàúÏúÑ</span>
            <span class="meta-value">
              {{ priorityLabel(submitInfo.level) || "-" }}
            </span>
          </div>

          <!-- 5. Í≥ÑÌöçÏûëÏÑ±Ïùº -->
          <div class="meta-item">
            <span class="meta-label">Í≥ÑÌöçÏûëÏÑ±Ïùº</span>
            <span class="meta-value">
              <MaterialButton color="dark" size="sm" @click="openPlanDetail">
                {{ formattedPlanSubmitAt }}
              </MaterialButton>
            </span>
          </div>

          <!-- 6. Ïã§Ï†ú ÏßÑÌñâÍ∏∞Í∞Ñ -->
          <div class="meta-item">
            <span class="meta-label">Ïã§Ï†ú ÏßÑÌñâÍ∏∞Í∞Ñ</span>
            <span class="meta-value period-value">
              <input
                type="month"
                v-model="mainForm.actualStart"
                class="input input-month"
              />
              <span class="mx-1">~</span>
              <input
                type="month"
                v-model="mainForm.actualEnd"
                class="input input-month"
              />
            </span>
          </div>
        </div>
      </div>

      <!-- Î©îÏù∏ Í≤∞Í≥º Ïπ¥Îìú -->
      <div class="card-block space-y-4">
        <!-- Í≤∞Í≥º Î™©Ìëú -->
        <div class="form-group">
          <label class="block text-sm mb-1 font-medium">Í≥ÑÌöçÌñàÎçò Î™©Ìëú</label>

          <select v-model="mainForm.goal" class="input goal-select">
            <option value="">Í≥ÑÌöçÏÑú Î™©Ìëú ÏÑ†ÌÉù</option>
            <option v-for="goal in planGoals" :key="goal" :value="goal">
              {{ goal }}
            </option>
          </select>
        </div>

        <!-- Í≤∞Í≥º ÎÇ¥Ïö© (ÏùºÎ∞òÏö©) -->
        <div class="form-group">
          <label class="block text-sm mb-1 font-medium">
            Í≤∞Í≥º ÎÇ¥Ïö© (ÏùºÎ∞òÏö©)
          </label>
          <MaterialTextarea
            id="result-content-public"
            variant="outline"
            :rows="4"
            placeholder="ÎåÄÏÉÅÏûê/ÏùºÎ∞òÏö© Í≤∞Í≥º ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
            :value="mainForm.publicContent"
            @input="(e) => (mainForm.publicContent = e.target.value)"
          />
        </div>

        <!-- Í≤∞Í≥º ÎÇ¥Ïö© (Í¥ÄÎ¶¨ÏûêÏö©) -->
        <div class="form-group">
          <label class="block text-sm mb-1 font-medium">
            Í≤∞Í≥º ÎÇ¥Ïö© (Í¥ÄÎ¶¨ÏûêÏö©)
          </label>
          <MaterialTextarea
            id="result-content-private"
            variant="outline"
            :rows="4"
            placeholder="Í¥ÄÍ≥ÑÏûê/Í¥ÄÎ¶¨ÏûêÏö© ÎÇ¥Î∂Ä Í≤∞Í≥º ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
            :value="mainForm.privateContent"
            @input="(e) => (mainForm.privateContent = e.target.value)"
          />
        </div>

        <!-- Ï≤®Î∂Ä ÌååÏùº -->
        <div class="form-group">
          <label class="block text-sm mb-1 font-medium">Ï≤®Î∂Ä ÌååÏùº</label>
          <input
            ref="fileInputRef"
            type="file"
            multiple
            @change="onMainFilesChange"
            class="file-input"
          />
          <p class="mt-1 text-xs text-gray-500">
            * Ïó¨Îü¨ Í∞ú ÌååÏùºÏùÑ Ìïú Î≤àÏóê ÏÑ†ÌÉùÌïòÍ±∞ÎÇò, ÎÇòÎà†ÏÑú Ïó¨Îü¨ Î≤à ÏÑ†ÌÉùÌï† Ïàò
            ÏûàÏäµÎãàÎã§.
          </p>

          <!-- Í∏∞Ï°¥ Ï≤®Î∂ÄÌååÏùº -->
          <ul
            v-if="existingFiles.length"
            class="mt-2 text-xs text-gray-700 space-y-1"
          >
            <li
              v-for="file in existingFiles"
              :key="file.attachCode"
              class="file-row"
            >
              <div class="file-main">
                <a
                  :href="file.url"
                  target="_blank"
                  rel="noopener noreferrer"
                  class="file-link"
                >
                  {{ file.originalFilename }}
                </a>

                <!-- ÏÇ≠Ï†ú ÏòàÏ†ï ÌëúÏãú -->
                <span
                  v-if="removedAttachCodes.includes(file.attachCode)"
                  class="file-tag-removed"
                >
                  ÏÇ≠Ï†ú ÏòàÏ†ï
                </span>
              </div>

              <!-- ÏÇ≠Ï†ú Î≤ÑÌäº -->
              <button
                v-if="!removedAttachCodes.includes(file.attachCode)"
                type="button"
                class="chip-button"
                @click="markFileForDelete(file.attachCode)"
              >
                ÏÇ≠Ï†ú
              </button>
            </li>
          </ul>

          <!-- ÏÉàÎ°ú ÏÑ†ÌÉùÌïú ÌååÏùº Î™©Î°ù -->
          <ul
            v-if="mainFiles.length"
            class="mt-2 text-xs text-gray-700 space-y-1"
          >
            <li
              v-for="(file, idx) in mainFiles"
              :key="file.name + '_' + file.lastModified + '_' + idx"
              class="file-row"
            >
              <span class="file-link">
                {{ file.name }} ({{ (file.size / 1024).toFixed(1) }} KB)
              </span>
              <button
                type="button"
                class="chip-button"
                @click="removeMainFile(idx)"
              >
                ÏÇ≠Ï†ú
              </button>
            </li>
          </ul>
        </div>
      </div>

      <!-- Ï∂îÍ∞Ä Í≤∞Í≥º Ïπ¥ÎìúÎì§ -->
      <div
        v-for="item in resultItems"
        :key="item.id"
        class="record-card space-y-4"
      >
        <div class="record-header">
          <h4 class="font-medium text-sm">Ï∂îÍ∞Ä Í≤∞Í≥º</h4>

          <MaterialButton
            color="dark"
            size="sm"
            variant="outlined"
            @click="removeResultItem(item.id)"
          >
            Ï†úÍ±∞
          </MaterialButton>
        </div>

        <div class="form-group">
          <label class="block text-sm mb-1 font-medium">Í≥ÑÌöçÌñàÎçò Î™©Ìëú</label>

          <select v-model="item.goal" class="input goal-select">
            <option value="">Ï∂îÍ∞Ä Í≤∞Í≥ºÏùò Í≥ÑÌöç Î™©Ìëú ÏÑ†ÌÉù</option>
            <option v-for="goal in planGoals" :key="goal" :value="goal">
              {{ goal }}
            </option>
          </select>
        </div>

        <div class="form-group">
          <label class="block text-sm mb-1 font-medium">
            Í≤∞Í≥º ÎÇ¥Ïö© (ÏùºÎ∞òÏö©)
          </label>
          <MaterialTextarea
            :id="`result-item-public-${item.id}`"
            variant="outline"
            :rows="3"
            placeholder="ÎåÄÏÉÅÏûê/ÏùºÎ∞òÏö© Í≤∞Í≥º ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
            :value="item.publicContent"
            @input="(e) => (item.publicContent = e.target.value)"
          />
        </div>

        <div class="form-group">
          <label class="block text-sm mb-1 font-medium">
            Í≤∞Í≥º ÎÇ¥Ïö© (Í¥ÄÎ¶¨ÏûêÏö©)
          </label>
          <MaterialTextarea
            :id="`result-item-private-${item.id}`"
            variant="outline"
            :rows="3"
            placeholder="Í¥ÄÍ≥ÑÏûê/Í¥ÄÎ¶¨ÏûêÏö© Í≤∞Í≥º ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
            :value="item.privateContent"
            @input="(e) => (item.privateContent = e.target.value)"
          />
        </div>
      </div>

      <!-- ÌïòÎã® Î≤ÑÌäº ÎùºÏù∏ -->
      <div class="action-bar">
        <div class="left-actions">
          <MaterialButton color="dark" size="sm" @click="goBack">
            ÏàòÏ†ï Ï∑®ÏÜå
          </MaterialButton>

          <MaterialButton color="dark" size="sm" @click="addResultItem">
            + Í≤∞Í≥º Ï∂îÍ∞Ä
          </MaterialButton>
        </div>

        <div class="right-actions">
          <MaterialButton
            color="dark"
            size="sm"
            class="ml-auto shrink-0"
            @click="submitAll"
          >
            {{ isResubmit ? "Ïû¨ÏûëÏÑ± ÏôÑÎ£å" : "ÏàòÏ†ï ÏôÑÎ£å" }}
          </MaterialButton>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";
import axios from "axios";

import MaterialButton from "@/components/MaterialButton.vue";
import MaterialTextarea from "@/components/MaterialTextarea.vue";

const route = useRoute();
const router = useRouter();

const user = JSON.parse(localStorage.getItem("user") || "{}");
const modifier = Number(user.user_code || 0);

const planGoals = ref([]);

// ÎùºÏö∞ÌÑ∞ÏóêÏÑú Î∞õÏùÄ Í∞íÎì§
const resultCode = Number(route.params.resultCode || 0);
const planCode = ref(Number(route.query.planCode || 0));
const submitCode = Number(route.query.submitCode || 0);

// ‚úÖ Í∏∞Î≥∏ Ï†ïÎ≥¥ (ÏûëÏÑ± ÌôîÎ©¥Í≥º ÎèôÏùº Íµ¨Ï°∞)
const submitInfo = ref({
  childName: "",
  guardianName: "",
  assigneeName: "",
  disabilityType: "",
  planSubmitAt: "",
  level: "",
});

const formattedPlanSubmitAt = computed(() => {
  const v = submitInfo.value.planSubmitAt;
  return v ? String(v).slice(0, 10) : "-";
});

// Î©îÏù∏ Í≤∞Í≥º Ìèº
const mainForm = ref({
  resultDate: "", // Í≤∞Í≥º ÏûëÏÑ±Ïùº
  actualStart: "",
  actualEnd: "",
  goal: "",
  publicContent: "",
  privateContent: "",
});

// Ï≤®Î∂Ä ÌååÏùºÎì§
const mainFiles = ref([]);
const fileInputRef = ref(null);

// Í∏∞Ï°¥ Ï≤®Î∂ÄÌååÏùº
const existingFiles = ref([]);
// ÏÇ≠Ï†ú ÏòàÏ†ï Ï≤®Î∂ÄÏΩîÎìú
const removedAttachCodes = ref([]);

// Ï∂îÍ∞Ä Í≤∞Í≥º Î™©Î°ù
const resultItems = ref([]);

const loading = ref(false);
const error = ref("");
const status = ref("");

// Î∞òÎ†§Ïù∏ Í≤ΩÏö∞ Ïû¨ÏûëÏÑ± Î™®Îìú
const isResubmit = computed(() => status.value === "CD7");

// Ïò§Îäò ÎÇ†Ïßú YYYY-MM-DD
function getTodayStr() {
  const d = new Date();
  return d.toISOString().slice(0, 10);
}

// Îç∞Ïù¥ÌÑ∞ Î°úÎî©
async function loadData() {
  loading.value = true;
  error.value = "";

  try {
    if (!submitCode) {
      throw new Error("submitCodeÍ∞Ä ÏóÜÏäµÎãàÎã§. (ÏøºÎ¶¨Î°ú Ï†ÑÎã¨ ÌïÑÏöî)");
    }
    if (!resultCode) {
      throw new Error("resultCodeÍ∞Ä ÏóÜÏäµÎãàÎã§. (paramsÎ°ú Ï†ÑÎã¨ ÌïÑÏöî)");
    }

    // Í∏∞Î≥∏ Ï†ïÎ≥¥ + Í≤∞Í≥º ÏÉÅÏÑ∏Î•º ÎèôÏãúÏóê ÏöîÏ≤≠
    const [basicRes, detailRes] = await Promise.all([
      axios.get(`/api/result/${submitCode}`), // ÏûëÏÑ± ÌôîÎ©¥Í≥º ÎèôÏùº API
      axios.get(`/api/result/detail/${resultCode}`),
    ]);

    // 1) ÏßÄÏõêÏûê Í∏∞Î≥∏ Ï†ïÎ≥¥
    const basic = basicRes.data;
    if (!basic?.success || !basic.result) {
      throw new Error(basic?.message || "ÏßÄÏõêÏûê Í∏∞Î≥∏ Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.");
    }
    const basicResData = basic.result;

    submitInfo.value = {
      childName: basicResData.childName || "",
      guardianName: basicResData.guardianName || "",
      assigneeName: basicResData.assigneeName || "",
      disabilityType: basicResData.disabilityType || "",
      planSubmitAt: basicResData.planSubmitAt || "",
      level: basicResData.level || "",
    };

    // 2) Í≤∞Í≥º ÏÉÅÏÑ∏ Ï†ïÎ≥¥
    const detail = detailRes.data;
    if (!detail?.success || !detail.result) {
      throw new Error(detail?.message || "ÏßÄÏõêÍ≤∞Í≥º Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.");
    }
    const d = detail.result;

    // planCodeÎ•º ÏùëÎãµÏóêÏÑú ÏµúÎåÄÌïú ÎΩëÏïÑÎÇ¥Í∏∞
    planCode.value = Number(
      d.main?.planCode ??
        d.main?.plan_code ??
        d.planCode ??
        d.plan_code ??
        planCode.value ??
        0
    );

    mainForm.value = {
      resultDate: d.main?.resultDate
        ? String(d.main.resultDate).slice(0, 10)
        : getTodayStr(),
      actualStart: d.main?.actualStart || "",
      actualEnd: d.main?.actualEnd || "",
      goal: d.main?.goal || "",
      publicContent: d.main?.publicContent || "",
      privateContent: d.main?.privateContent || "",
    };

    status.value = d.status || "";

    resultItems.value =
      (d.items || []).map((it, idx) => ({
        id: it.resultItemCode || Date.now() + idx,
        goal: it.goal || "",
        publicContent: it.publicContent || "",
        privateContent: it.privateContent || "",
      })) || [];

    existingFiles.value =
      (d.attachments || []).map((a) => ({
        attachCode: a.attachCode,
        originalFilename: a.originalFilename,
        url: a.url,
      })) || [];

    // üîπ Í≥ÑÌöç Î™©Ìëú Î™©Î°ù ÏÑ∏ÌåÖ
    if (Array.isArray(basicResData.planGoals)) {
      const goals = basicResData.planGoals
        .map((g) => (g || "").trim())
        .filter((g) => g);

      let uniq = [...new Set(goals)];

      // ÏàòÏ†ï ÌôîÎ©¥ÏóêÏÑú Í∏∞Ï°¥ Ï†ÄÏû•Îêú goalÏù¥ Î™©Î°ùÏóê ÏóÜÏúºÎ©¥ Ìè¨Ìï®ÏãúÏºúÏ£ºÍ∏∞
      if (mainForm.value.goal && !uniq.includes(mainForm.value.goal)) {
        uniq = [mainForm.value.goal, ...uniq];
      }

      planGoals.value = uniq;
    } else {
      // planGoalsÍ∞Ä ÏïÑÏòà Ïïà ÎÇ¥Î†§Ïò§ÎäîÎç∞ Í∏∞Ï°¥ goalÏù¥ ÏûàÏúºÎ©¥ Í∑∏Í±∞ÎùºÎèÑ Î≥¥Ïó¨Ï£ºÍ∏∞
      planGoals.value = mainForm.value.goal ? [mainForm.value.goal] : [];
    }
  } catch (e) {
    console.error(e);
    error.value = e.message || "ÏßÄÏõêÍ≤∞Í≥º Ï†ïÎ≥¥ Ï°∞Ìöå Ï§ë Ïò§Î•ò";
  } finally {
    loading.value = false;
  }
}

function priorityLabel(code) {
  const c = (code || "").toUpperCase();
  switch (c) {
    case "BB1":
      return "Í∏¥Í∏â";
    case "BB2":
      return "Ï§ëÏ†ê";
    case "BB3":
      return "Í≥ÑÌöç";
    default:
      return "-";
  }
}

onMounted(() => {
  if (!mainForm.value.resultDate) {
    mainForm.value.resultDate = getTodayStr();
  }
  loadData();
});

// ÌååÏùº Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨
function onMainFilesChange(e) {
  const files = Array.from(e.target.files || []);

  const newOnes = files.filter(
    (f) =>
      !mainFiles.value.some(
        (ex) =>
          ex.name === f.name &&
          ex.size === f.size &&
          ex.lastModified === f.lastModified
      )
  );

  mainFiles.value = [...mainFiles.value, ...newOnes];

  if (e.target) {
    e.target.value = "";
  }
}

// ÌååÏùº Í∞úÎ≥Ñ ÏÇ≠Ï†ú
function removeMainFile(index) {
  mainFiles.value.splice(index, 1);
}

// Í∏∞Ï°¥ Ï≤®Î∂Ä ÏÇ≠Ï†ú ÌëúÏãú
function markFileForDelete(attachCode) {
  if (!removedAttachCodes.value.includes(attachCode)) {
    removedAttachCodes.value.push(attachCode);
  }
}

// Í≥ÑÌöç ÏÉÅÏÑ∏Î°ú Ïù¥Îèô
function openPlanDetail() {
  if (!planCode.value) {
    alert(
      "planCodeÍ∞Ä ÏóÜÏäµÎãàÎã§. (Í≤∞Í≥º ÏÉÅÏÑ∏ ÏùëÎãµÏóêÏÑú planCodeÎ•º ÎÇ¥Î†§Ï£ºÎäîÏßÄ ÌôïÏù∏ ÌïÑÏöî)"
    );
    return;
  }
  let url = `/plans/detail/${planCode.value}`;
  if (submitCode) {
    url += `?submitCode=${submitCode}`;
  }
  window.open(url, "_blank");
}

// Î™©Î°ùÏúºÎ°ú ÎèåÏïÑÍ∞ÄÍ∏∞
function goBack() {
  router.push({ name: "resultList" });
}

// Ï∂îÍ∞Ä Í≤∞Í≥º Î∏îÎ°ù Ï∂îÍ∞Ä
function addResultItem() {
  resultItems.value.push({
    id: Date.now(),
    goal: "",
    publicContent: "",
    privateContent: "",
  });
}

// Ï∂îÍ∞Ä Í≤∞Í≥º ÏÇ≠Ï†ú
function removeResultItem(id) {
  resultItems.value = resultItems.value.filter((p) => p.id !== id);
}

// ‚úÖ Ïú†Ìö®ÏÑ± Ï≤¥ÌÅ¨ (ÏûëÏÑ± ÌôîÎ©¥Í≥º ÎèôÏùºÌïòÍ≤å Ïã§Ï†ú ÏßÑÌñâÍ∏∞Í∞Ñ Ìè¨Ìï®)
function validate() {
  if (!mainForm.value.actualStart) {
    return "Ïã§Ï†ú ÏßÑÌñâÍ∏∞Í∞Ñ ÏãúÏûë ÏõîÏùÑ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.";
  }
  if (!mainForm.value.actualEnd) {
    return "Ïã§Ï†ú ÏßÑÌñâÍ∏∞Í∞Ñ Ï¢ÖÎ£å ÏõîÏùÑ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.";
  }
  if (mainForm.value.actualStart > mainForm.value.actualEnd) {
    return "Ïã§Ï†ú ÏßÑÌñâÍ∏∞Í∞ÑÏùò ÏãúÏûë ÏõîÏù¥ Ï¢ÖÎ£å ÏõîÎ≥¥Îã§ Îä¶ÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.";
  }

  if (!mainForm.value.goal.trim()) return "Í≤∞Í≥º Î™©ÌëúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.";
  if (!mainForm.value.publicContent.trim())
    return "Í≤∞Í≥º ÎÇ¥Ïö©(ÏùºÎ∞òÏö©)ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.";
  if (!mainForm.value.privateContent.trim())
    return "Í≤∞Í≥º ÎÇ¥Ïö©(Í¥ÄÎ¶¨ÏûêÏö©)ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.";

  for (const p of resultItems.value) {
    if (!p.goal.trim()) return "Ï∂îÍ∞Ä Í≤∞Í≥ºÏùò Î™©ÌëúÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.";
    if (!p.publicContent.trim())
      return "Ï∂îÍ∞Ä Í≤∞Í≥ºÏùò ÎÇ¥Ïö©(ÏùºÎ∞òÏö©)ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.";
    if (!p.privateContent.trim())
      return "Ï∂îÍ∞Ä Í≤∞Í≥ºÏùò ÎÇ¥Ïö©(Í¥ÄÎ¶¨ÏûêÏö©)ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.";
  }

  return null;
}

// ÏàòÏ†ï ÏôÑÎ£å
async function submitAll() {
  const err = validate();
  if (err) {
    alert(err);
    return;
  }

  try {
    const formJson = {
      resultCode,
      planCode: planCode.value,
      submitCode,
      mainForm: mainForm.value,
      resultItems: resultItems.value,
      removedAttachCodes: removedAttachCodes.value,
      modifier,
    };

    const formData = new FormData();
    formData.append("formJson", JSON.stringify(formJson));

    mainFiles.value.forEach((file) => {
      formData.append("resultFiles", file);
    });

    const res = await axios.put(`/api/result/${resultCode}`, formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });

    if (!res.data?.success) {
      alert(res.data.message || "ÏàòÏ†ï Ïã§Ìå®");
      return;
    }

    // CD7(Î∞òÎ†§)ÏóêÏÑú Ïû¨ÏûëÏÑ± ÏôÑÎ£åÏù∏ Í≤ΩÏö∞ ‚Üí Ïû¨ÏäπÏù∏ÏöîÏ≤≠
    if (isResubmit.value) {
      await axios.post(`/api/result/${resultCode}/resubmit`, {
        requesterCode: 2, // TODO: ÎÇòÏ§ëÏóê Î°úÍ∑∏Ïù∏ ÏÑ∏ÏÖòÍ∞íÏúºÎ°ú ÍµêÏ≤¥
      });
      alert("Ïû¨ÏûëÏÑ±Îêú ÏßÄÏõêÍ≤∞Í≥ºÍ∞Ä Ïû¨ÏäπÏù∏ ÏöîÏ≤≠ÏúºÎ°ú Ïò¨ÎùºÍ∞îÏäµÎãàÎã§.");
    } else {
      alert("ÏßÄÏõêÍ≤∞Í≥ºÍ∞Ä ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§.");
    }

    router.push({ name: "resultList" });
  } catch (e) {
    console.error(e);
    alert(
      "ÏàòÏ†ï Ï≤òÎ¶¨ Ï§ë ÏÑúÎ≤Ñ Ïò§Î•ò: " + (e.response?.data?.message || e.message)
    );
  }
}
</script>

